@import 'mixin';


body {
    margin: 0px auto;
    font-size: 100%;
}

.hero-header {



    .navbar {
        background-color: #D0E5F0;
        background-image: url(../../Design/Assets/png/bar-1.png);
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin: 0px auto;
        width: 100%;


        .navbar_logo {
            width: 5.56rem;
            margin-left: 40px;

            @include responsive(phone) {
                margin-left: 7px;

            }
        }


        .navbar_btn {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;



            a {
                text-decoration: none;
                margin-left: 70px;
                font-weight: bold;
                font-size: 1.7rem;
                color: #1A519E;

                @include responsive(phone) {
                    margin-left: 20px;

                }



            }

            .arabi {
                @include head-foot-btn(#1A519E);
                margin-left: 70px;
                margin-right: 50px;

                @include responsive(phone) {
                    margin-left: 20px;
                    margin-right: 7px;


                }

                img {
                    margin-left: 1.25rem;
                }

            }
        }

    }

    .p-header {
        background-color: #F4CF1B;
        text-align: center;
        height: auto;



        h3 {
            font-weight: 700;
            font-size: 1.1rem;

            @include responsive(phone) {
                font-size: 1.4rem;

            }

        }

        p {
            color: #1078AD;
            font-weight: bold;
            font-size: 1rem;

            @include responsive(phone) {
                font-size: 1.6rem;

            }
        }

    }
}